import { Component } from '@angular/core';
import { IonicPage, ViewController, NavParams } from 'ionic-angular';
import { Inspecao } from '../../../models/inspecao';
import { DatePicker } from '@ionic-native/date-picker';

@IonicPage()
@Component({
    selector: 'page-manter-verificacao',
    templateUrl: 'manter-verificacao.html'
})

export class ManterVerificacaoPage {

    inspecao: Inspecao = new Inspecao();

    constructor(
        public viewCtrl: ViewController,
        public navParams: NavParams,
        private datePicker: DatePicker
    ) {
        this.inspecao = this.navParams.data.inspecao;
    }

    obterDataInspecao() {
        this.datePicker.show({
            date: new Date(),
            mode: 'date'
        }).then(
            date => { this.inspecao.dataInspecao = date }
        );
    }

    obterDataEncerramento() {
        this.datePicker.show({
            date: new Date(),
            mode: 'date'
        }).then(
            date => { this.inspecao.dataEncerramento = date }
        );
    }

    voltar() {
        this.viewCtrl.dismiss(null);
    }

}



